import Link from 'next/link';

function LoggedInNavbar(userConfig) {
  return (
    <nav className="uk-navbar-container" uk-navbar="true">
      <div className="uk-navbar-left">
        <Link href="/">
          <a>
            <img class="uk-navbar-item uk-logo logo" src="/static/images/mediasummon-logo-transp.png" alt="Mediasummon Logo"/>
            Mediasummon
          </a>
        </Link>
        <ul className="uk-navbar-nav">
          {/*
          <li className="logo">
            <Link href="/">
              <img src="/static/images/mediasummon-logo-transp.png" alt="Mediasummon logo" />
            </Link>
          </li>
          */}
          <li>
            <Link href="/">
              <a>Dashboard</a>
            </Link>
          </li>
        </ul>
      </div>
      <div className="uk-navbar-right">
        <ul className="uk-navbar-nav">
          <li>
            <Link href="/logout">
              <a>Logout</a>
            </Link>
          </li>
        </ul>
      </div>
      <style jsx>{`
        img {
          max-width: 72px !important;
          max-height: 72px !important;
        }
      `}</style>
    </nav>
  );
}

function LoggedOutNavbar() {
  return (
    <nav className="uk-navbar-container" uk-navbar="true">
      <div className="uk-navbar-right">
        <ul className="uk-navbar-nav">
          <li>
            <Link href="/login">
              <a>Login</a>
            </Link>
          </li>
        </ul>
      </div>
    </nav>
  );
}

export default function Navbar({ userConfig }) {
  return userConfig ? LoggedInNavbar(userConfig) : LoggedOutNavbar();
}